.user-page {
    @extend %first-block;
    &__inner {
        @extend %container;
        display: grid;
        grid-template-columns: 20rem 1fr;
        gap: 4rem;
        @include xs {
            grid-template-columns: 1fr;
            gap: 1.6rem;
        }
        @include sm {
            grid-template-columns: 10rem 1fr;
        }
        @include md {
            grid-template-columns: 12rem 1fr;
        }
        @include lg {
            grid-template-columns: 18rem 1fr;
        }
        @include xl {
            grid-template-columns: 18rem 1fr;
        }
    }
}