.course-detail {
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        margin: 0 auto;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5.6rem;
        @include xs {
            grid-template-columns: 1fr;
            width: 100%;
            gap: 2.4rem;
        }
        @include sm {
            width: 100%;
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include md {
            width: 100%;
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
    }
    &__kv {
        @extend %site-img;
    }
    &__intro {
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
        h3 {
            font-size: clamp(var(--font-size-lg), 4vw, var(--font-size-3xl));
            color: var(--color-main-800);
            line-height: 1.6;
        }
    }
    &__label {
        @extend %sm-label;
    }
    &__content {
        display: grid;
        grid-template-columns: 1fr;
        gap: 5.6rem;
    }
    &__info {
        @extend %post-info;
        flex-direction: column;
    }
    &__block {
        display: flex;
        flex-direction: column;
    }
    &__content-inner {
        padding-top: 2rem;
        p {
            font-size: var(--font-size-base);
            line-height: 1.6;
            color: var(--color-main-700);
            letter-spacing: 0.2rem;
            margin-bottom: 2.4rem;
        }
        h5 {
            font-size: var(--font-size-lg);
            color: var(--color-main-800);
            line-height: 1.6;
            margin-bottom: 0.8rem;
        }
        ul {
            padding-left: 2rem;
            list-style: disc;
            &:not(:last-child) {
                margin-bottom: 3.2rem;
            }
            li {
                font-size: var(--font-size-base);
                line-height: 1.6;
                color: var(--color-main-700);
                letter-spacing: 0.2rem;
                &:not(:last-child) {
                    margin-bottom: 0.8rem;
                }
            }
        }
    }
    &__nav {
        display: flex;
        gap: 1.6rem;
        width: 100%;
    }
    &__btn-order,
    &__btn-back,
    &__btn-link {
        @extend %btn-lg;
    }
    &__teacher {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        padding: 1.6rem;
        border-radius: 1.2rem;
        border: solid 0.1rem var(--color-main-200);
        background-color: var(--color-main-100);
    }
    &__teacher-profile {
        display: grid;
        gap: 0 1.6rem;
        align-items: center;
        grid-template-columns: 5.6rem 1fr;
        grid-template-rows: repeat(2, 1fr);
    }
    &__teacher-intro {
        @extend %sub-body-text;
    }
    &__teacher-image {
        @extend %site-img;
        width: 5.6rem;
        height: 5.6rem;
        border-radius: 50%;
        overflow: hidden;
        grid-column: 1 / 2;
        grid-row: 1 / 3;
    }
    &__teacher-name {
        font-size: var(--font-size-xl);
        color: var(--color-main-800);
        font-weight: 900;
    }
    &__teacher-aka {
        font-size: var(--font-size-base);
        color: var(--color-main-600);
    }
    &__item {
        display: flex;
        gap: 0.8rem;
        flex-direction: column;
        &:not(:last-child) {
            border-bottom: 0.1rem solid var(--color-main-300);
            padding-bottom: 1.6rem;
        }
        h4 {
            font-size: var(--font-size-base);
            color: var(--color-main-800);
            line-height: 1.6;
            font-weight: 500;
        }
    }
    &__item-wrap {
        gap: 1.6rem;
        display: flex;
    }
    &__item-text {
        font-size: var(--font-size-lg);
        color: var(--color-main-800);
        line-height: 1.6;
        font-weight: 900;
    }
    &__item-method {
        border-radius: 0.8rem;
        background-color: var(--color-main-200);
        padding: 0.8rem 1.2rem;
        color: var(--color-main-700);
        font-size: var(--font-size-base);
        border: solid 0.1rem var(--color-main-300);
    }
    &__item-text-price {
        font-size: var(--font-size-4xl);
        color: var(--color-danger-700);
        font-weight: 900;
    }
    &__course-content {
        h4 {
            font-size: var(--font-size-base);
            color: var(--color-main-800);
            line-height: 1.6;
            font-weight: 500;
        }
    }
}