.site-footer {
    $self: &;
    &__content {
        padding-top: 4rem;
        padding-bottom: 4rem;
        background-color: var(--color-main-800);
        #{$self}__inner {
            align-items: flex-end;
        }
    }
    &__copyright {
        padding-top: 4rem;
        padding-bottom: 4rem;
        background-color: var(--color-main-900);
    }
    &__inner {
        @extend %container;
        display: flex;
        justify-content: space-between;
        @include xs {
            flex-direction: column;
            gap: 2.4rem;
        }
        @include sm {
            flex-direction: column;
            gap: 2.4rem;
        }
    }
    &__block {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        @include xs {
            width: 100%;
        }
        @include sm {
            width: 100%;
        }
    }
    &__sns {
        display: flex;
        justify-content: flex-end;
        gap: 1.2rem;
        @include xs {
            justify-content: center;
        }
        @include sm {
            justify-content: center;
        }
        a {
            width: 2.4rem;
            img {
                max-width: 100%;
                display: block;
                margin: 0;
            }
        }
    }
    &__policy {
        display: flex;
        justify-content: flex-end;
        gap: 1.2rem;
        @include xs {
            justify-content: center;
        }
        @include sm {
            justify-content: center;
        }
        a {
            font-size: clamp(var(--font-size-sm), -0.875rem + 8.333vw, var(--font-size-sm));
            color: var(--color-main-100);
            transition: color 300ms ease;
            &:hover {
                color: var(--color-main-300);
            }
        }
    }
    &__info {
        width: 40rem;
        @include xs {
            width: 100%;
            padding-left: 2.4rem;
            padding-right: 2.4rem;
        }
        @include sm {
            width: 100%;
        }
        h2 {
            font-size: clamp(var(--font-size-base), -0.875rem + 8.333vw, var(--font-size-2xl));
            color: var(--color-main-100);
            line-height: 1.6;
            @include xs {
                font-size: var(--font-size-xl);
                text-align: center;
            }
            @include sm {
                text-align: center;
            }
        }
        p {
            font-size: clamp(var(--font-size-sm), -0.875rem + 8.333vw, var(--font-size-sm));
            color: var(--color-main-100);
            line-height: 1.6;
            @include xs {
                text-align: center;
            }
            @include sm {
                text-align: center;
            }
        }
    }
    &__copyright-text {
        color: var(--color-main-100);
        font-size: clamp(var(--font-size-sm), -0.875rem + 8.333vw, var(--font-size-sm));
    }
    &__copyright {
        #{$self}__inner {
            align-items: center;
        }
    }
    &__btn-gotop {
        overflow: hidden;
        position: relative;
        font-size: 0;
        border-radius: 50%;
        background-color: transparent;
        width: 3.2rem;
        height: 3.2rem;
        border: solid 0.2rem var(--color-main-100);
        &::before,
        &::after {
            width: 0.8rem;
            height: 0.8rem;
            position: absolute;
            border-top: solid 0.2rem var(--color-main-100);
            border-right: solid 0.2rem var(--color-main-100);
            content: "";
            left: 50%;
            top: 50%;
            transition: transform 300ms ease;
        }
        &::before {
            transform: translate3d(-50%,-30%,0) rotate(-45deg);
        }
        &::after {
            transform: translate3d(-50%,200%,0) rotate(-45deg);
        }
        &:hover {
            &::before {
                transform: translate3d(-50%,-250%,0) rotate(-45deg);
            }
            &::after {
                transform: translate3d(-50%,-50%,0) rotate(-45deg);
            }
        }
    }
}