.pet-profile {
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        margin: 0 auto;
        width: 72rem;
        display: grid;
        grid-template-columns: 20rem 1fr;
        gap: 5.6rem;
        &.password {
            grid-template-columns: 1fr;
        }
        @include xs {
            gap: 2.4rem;
            width: 100%;
            grid-template-columns: 1fr;
        }
        @include sm {
            gap: 2.4rem;
            width: 100%;
            grid-template-columns: 1fr;
        }
        @include md {
            gap: 2.4rem;
            width: 100%;
            grid-template-columns: 1fr;
        }
    }
    &__headshot {
        @extend %profile-headshot;
    }
    &__btn-upload {
        @extend %site-btn-upload;
    }
    &__profile {
        @extend %profile-profile;
    }
    &__row {
        @extend %form-row;
        label {
            @extend %site-label;
        }
        input {
            @extend %site-input;
        }
    }
    &__btn-update {
        @extend %btn-lg;
    }
    &__select-wrap {
        @extend %site-select;
    }
    &__text {
        font-size: var(--font-size-base);
        font-weight: 900;
        color: var(--color-main-900);
        border: dotted 0.2rem transparent;
        border-radius: 1.2rem;
    }
    &__chronic-wrap,
    &__radio-wrap,
    &__checkbox-wrap {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 1.6rem;
        &.flex-column {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__checkbox {
        @extend %site-checkbox;
    }
    &__radio {
        width: 100%;
        @extend %site-radio;
    }
    &__btn-add {
        @extend %btn-lg;
    }
    &__btn-new {
        @extend %btn-lg;
    }
    &__btn-download {
        @extend %btn-lg;
    }
    &__btn-delete {
        width: 1.8rem;
        height: 1.8rem;
        border-radius: 50%;
        background: var(--color-main-800) url('../images/icon-delete.svg') no-repeat center center;
        background-size: 0.8rem;
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0;
        border: 0;
        transform: translate3d(40%,-40%,0);
    }
    &__drug-input-wrap {
        display: flex;
        gap: 1.6rem;
        input {
            flex: 1;
        }
    }
    &__drug-wrap {
        padding-top: 0.8rem;
        display: flex;
        gap: 1.6rem;
        flex-wrap: wrap;
    }
    &__drug-item {
        position: relative;
        span {
            border: solid 0.2rem var(--color-main-300);
            color: var(--color-main-800);
            padding: 1.2rem 1.6rem;
            border-radius: 1.2rem;
            display: block;
            font-size: var(--font-size-base);
        }
    }
    &__name {
        padding-top: 2rem;
        text-align: center;
        font-size: var(--font-size-xl);
        color: var(--color-main-700);
    }
    &__info {
        padding-top: 0.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.8rem;
    }
    &__gender {
        width: 2.4rem;
        height: 2.4rem;
        background-size: 2.4rem;
        &.male {
            background-image: url('../images/icon-male.svg');
        }
        &.female {
            background-image: url('../images/icon-female.svg');
        }
        &.die.male {
            background-image: url('../images/icon-male-die.svg');
        }
        &.die.female {
            background-image: url('../images/icon-female-die.svg');
        }
    }
    &__animal,
    &__age {
        font-size: var(--font-size-base);
        color: var(--color-main-700);
    }
    &__journal {
        display: grid;
        grid-template-columns: 1fr;
        gap: 8rem;
    }
    &__btn-add-journal {
        @extend %btn-base;
        width: 12rem;
        text-align: center;
        margin: 0 auto;
    }
}

.pet-journal-post {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    &__main-title {
        color: var(--color-main-800);
        text-align: center;
        font-size: var(--font-size-4xl);
    }
    &__info {
        justify-content: center;
        @extend %post-info;
    }
    &__post-img {
        @extend %post-img;
        border-radius: 1.2rem;
        overflow: hidden;
    }
    &__content {
        p {
            @extend %main-post-text;
            text-indent: 3.6rem;
        }
    }
    &__control {
        display: flex;
        justify-content: flex-end;
        gap: 1.2rem;
    }
    &__btn-edit,
    &__btn-delete {
        @extend %btn-base;
    }
    &__btn-delete {
        background-color: var(--color-danger-500);
        &:hover {
            background-color: var(--color-danger-700);
        }
    }
}