.latest-articles {
    $self: &;
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
        gap: 4rem;
        @include xs {
            grid-template-columns: 1fr;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
        @include md {
            grid-template-columns: 1fr;
        }
        @include lg {
            grid-template-columns: repeat(3, 1fr);
        }
    }
    &__post-img {
        @extend %post-img;
    }
    &__post {
        @extend %post;
        &:nth-child(1) {
            grid-column: 1 / 2;
            grid-row: 1 / 4;
            flex-direction: column;
            #{$self}__post-detail {
                height: 100%;
            }
            @include lg {
                grid-column: 1 / 4;
            }
        }
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4) {
            flex-direction: row;
            #{$self}__post-img {
                flex: 1;
            }
            #{$self}__post-detail {
                flex: 1;
            }
            #{$self}__main-post-text {
                display: none;
            }
            @include xs {
                flex-direction: column;
            }
            @include sm {
                flex-direction: column;
            }
            @include lg {
                flex-direction: column;
            }
        }
    }
    &__post-detail {
        @extend %post-detail;
    }
    &__post-content {
        @extend %post-content;
    }
    &__main-post-title {
        @extend %main-post-title;
    }
    &__main-post-text {
        @extend %main-post-text;
    }
    &__tag-list {
        @extend %tag-list;
    }
    &__btn-tag {
        @extend %btn-tag;
    }
    &__info {
        @extend %post-info;
    }
}