.contact {
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        margin: 0 auto;
        width: 64rem;
        @include xs {
            width: 100%;
        }
    }
    &__label {
        color: var(--color-grey-600);
        font-size: var(--font-size-base);
        display: block;
    }
    &__input {
        @extend %site-input;
    }
    &__textarea {
        @extend %site-input;
        height: 20rem;
        line-height: 1.6;
    }
    &__form {
        display: grid;
        gap: 2rem;
    }
    &__row {
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        position: relative;
    }
    &__input-file {
        margin: 0;
        box-sizing: border-box;
        background-clip: padding-box;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        font-size: var(--font-size-sm);
        color: var(--color-main-800);
        border: 0.1rem solid var(--color-main-200);
        padding: 0.8rem;
        background-color: var(--color-main-200);
        border-radius: 1.2rem;
        &::file-selector-button {
            font-weight: bold;
            color: var(--color-main-800);
            padding: 0;
            border: 0;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            border-radius: 0.1px;
            padding: 0.8rem 1.2rem;
            font-size: var(--font-size-sm);
            background-color: var(--color-main-200);
            border-right: solid 0.1rem var(--color-main-300);
            transform: translate3d(-0.6rem,0,0);
        }
    }
    &__btn-submit {
        @extend %btn-lg;
    }
}