.course-list {
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__date {
        padding-bottom: 2.4rem;
        font-size: var(--font-size-2xl);
        color: var(--color-main-700);
        text-align: right;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 8rem 4rem;
        @include xs {
            grid-template-columns: 1fr;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
        @include md {
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__post-img {
        @extend %post-img;
    }
    &__post {
        @extend %post;
        position: relative;
        &.expired {
            filter: grayscale(1);
        }
    }
    &__label {
        @extend %sm-label;
    }
    &__post-detail {
        @extend %post-detail;
    }
    &__post-content {
        @extend %post-content;
    }
    &__main-post-title {
        @extend %main-post-title;
    }
    &__info {
        @extend %post-info;
    }
    &__row {
        @extend %post-row;
    }
    &__btn-more {
        @extend %btn-lg;
    }
    &__teacher-image {
        background-color: var(--color-main-200);
        @extend %post-img;
        width: 6.4rem;
        height: 6.4rem;
        border-radius: 50%;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate3d(20%,-50%,0);
        border: solid 0.2rem var(--color-main-200);
    }
}