.tammie-intro {
    $self: &;
    @include xs {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__inner-block {
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
    }
    &__block {
        display: flex;
        flex-direction: column;
        gap: 4rem;
        @include xs {
            gap: 2rem;
        }
        h3 {
            font-size: var(--font-size-2xl);
            color: var(--color-main-800);
            line-height: 1.6;
            letter-spacing: 0.2rem;
        }
        h4 {
            @extend %body-title;
        }
    }
    &__content {
        p {
            @extend %body-text;
        }
    }
}