.user-course {
    &__main-title {
        @extend %site-title;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 4rem;
        padding-top: 3.2rem;
        @include xs {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
        @include md {
            grid-template-columns: repeat(2, 1fr);
        }
        @include lg {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}