.pet-related-post {
    @include xs {
        padding: 0 2.4rem;
    }
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 4rem;
        @include xs {
            grid-template-columns: 1fr;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
    }
}