.tammie-book {
    $self: &;
    @include xs {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5.6rem;
        @include xs {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include sm {
            gap: 2.4rem;
        }
        @include md {
            gap: 2.4rem;
        }
        @include lg {
            gap: 2.4rem;
        }
    }
    &__item {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 4rem;
        @include xs {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include sm {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include md {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include lg {
            gap: 2.4rem;
        }
    }
    &__book {
        border-radius: 1.6rem;
        img {
            max-width: 100%;
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
        padding: 1.6rem 0;
        @include xs {
            padding-top: 0;
        }
        @include sm {
            padding-top: 0;
        }
        @include md {
            padding-top: 0;
        }
    }
    &__book-title {
        @extend %body-title;
    }
    &__book-description {
        @extend %sub-body-text;
    }
    &__detail {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1.2rem;
        margin-bottom: 0;
        margin-top: auto;
        border-top: 0.2rem dotted var(--color-main-300);
        padding-top: 2rem;
    }
    &__specification {
        span {
            &:nth-child(1) {
                font-size: var(--font-size-sm);
                color: var(--color-main-600);
                display: block;
                line-height: 1.6;
            }
            &:nth-child(2) {
                font-size: var(--font-size-base);
                color: var(--color-main-800);
            }
        }
    }
}