:root {
    font-size: 10px;
    --color-main-900: #2d2d41;
    --color-main-800: #585773;
    --color-main-700: #8F8CA9;
    --color-main-600: #AAA6C1;
    --color-main-500: #C5C0D9;
    --color-main-400: #D0CEE2;
    --color-main-300: #DCDBEA;
    --color-main-200: #E9E9F2;
    --color-main-150: #f1f1f8;
    --color-main-100: #F7F8FB;
    --color-grey-900: #040404;
    --color-grey-800: #242425;
    --color-grey-700: #464446;
    --color-grey-600: #676465;
    --color-grey-500: #878584;
    --color-grey-400: #A29F9F;
    --color-grey-300: #BCBABB;
    --color-grey-200: #D6D5D6;
    --color-grey-100: #F1F0F1;
    --color-danger-500: #DB585F;
    --color-danger-700: #bc4248;
    --color-vip-0: #4BAC5E;
    --color-vip-1: #8E583D;
    --color-vip-2: #717171;
    --color-vip-3: #5e8cac;
    --font-size-7xl: 9.6rem;
    --font-size-6xl: 6.4rem;
    --font-size-5xl: 4.8rem;
    --font-size-4xl: 3.6rem;
    --font-size-3xl: 3rem;
    --font-size-2xl: 2.4rem;
    --font-size-xl: 2rem;
    --font-size-lg: 1.8rem;
    --font-size-base: 1.6rem;
    --font-size-sm: 1.4rem;
    --font-size-xs: 1.2rem;
}

* {
    &::selection {
        background-color: var(--color-main-700);
        color: #fff;
    }
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &.fixed {
        overflow: hidden;
    }
}

body {
    background-color: var(--color-main-150);
    &.fixed {
        overflow: hidden;
    }
}

.wrap {
    display: grid;
    grid-template-rows: 1fr auto;
    min-height: 100vh;
    box-sizing: border-box;
    padding-top: 7.2rem;
    @include xs {
        padding-top: 6.4rem;
    }
}