.login-page {
    box-sizing: border-box;
    width: 100%;
    min-height: 100vh;
    position: relative;
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(4, 1fr);
    background-color: var(--color-main-300);
    @include xs {
        padding: 2.4rem;
        grid-template-rows: repeat(6, 1fr);
        height: 80rem;
    }
    @include sm {
        grid-template-rows: repeat(6, 1fr);
    }
    @include md {
        grid-template-rows: repeat(6, 1fr);
    }
    &__kv {
        width: 79.3rem;
        height: 76.7rem;
        position: absolute;
        z-index: 0;
        background: transparent url('../images/pet-bg.png') no-repeat 0 0;
        background-size: cover;
        left: 0;
        bottom: 0;
        @include xs {
            width: 79.3rem * 0.34;
            height: 76.7rem * 0.34;
        }
        @include sm {
            width: 79.3rem * 0.45;
            height: 76.7rem * 0.45;
            
        }
        @include md {
            width: 79.3rem * 0.4;
            height: 76.7rem * 0.4;
        }
        @include lg {
            width: 79.3rem * 0.55;
            height: 76.7rem * 0.55;
        }
        @include xl {
            width: 79.3rem * 0.7;
            height: 76.7rem * 0.7;
        }
        @include xxl {
            width: 79.3rem * 0.8;
            height: 76.7rem * 0.8;
        }
    }
    &__content {
        border-radius: 1.2rem;
        padding: 4rem;
        position: relative;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        grid-column: 10/12;
        grid-row: 2/4;
        background-color: var(--color-main-100);
        @include xs {
            grid-column: 1/13;
            grid-row: 1/5;
            padding: 3.2rem 2.4rem;
        }
        @include sm {
            grid-column: 3/11;
        }
        @include md {
            grid-column: 4/10;
            grid-row: 1/6;
            @media (orientation: landscape) {
                grid-column: 6/12;
                grid-row: 2/6;
            }
        }
        @include lg {
            grid-column: 8/12;
            grid-row: 1/5;
        }
        @include xl {
            grid-column: 8/12;
            grid-row: 1/6;
        }
        @include xxl {
            grid-column: 9/12;
        }
    }
}

.login-ui {
    width: 100%;
    margin: 0 auto;
    &__main-title {
        @extend %site-title;
    }
    &__checkbox {
        @extend %site-checkbox;
    }
    &__form {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__row {
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        position: relative;
        &--row {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__label {
        @extend %site-label;
    }
    &__input {
        @extend %site-input;
    }
    &__forgot {
        display: flex;
        gap: 1.2rem;
    }
    &__btn {
        font-size: var(--font-size-base);
        color: var(--color-main-700);
        transition: color 300ms ease;
        &:hover {
            color: var(--color-main-900);
        }
    }
    &__btn-submit {
        @extend %btn-lg;
    }
    &__three-party {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        margin-bottom: 2.4rem;
    }
    &__btn-facebook {
        @extend %btn-lg;
        text-align: center;
    }
    &__btn-google {
        @extend %btn-lg;
        text-align: center;
    }
    &__toggle-password {
        position: absolute;
        right: 1.2rem;
        bottom: 1.4rem;
        width: 2rem;
        height: 2rem;
        cursor: pointer;
        &::before,
        &::after {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            content: "";
            transition: opacity 300ms ease;
        }
        &::before {
            background: transparent url('../images/icon-show.svg') no-repeat 0 0;
            background-size: cover;
        }
        &::after {
            background: transparent url('../images/icon-hide.svg') no-repeat 0 0;
            background-size: cover;
            opacity: 0;
        }
        &.active {
            &::before {
                opacity: 0;
            }
            &::after {
                opacity: 1;
            }
        }
    }
    &__or {
        font-size: var(--font-size-sm);
        color: var(--color-main-500);
        display: grid;
        grid-template-columns: minmax(2rem, 1fr) auto minmax(2rem, 1fr);
        align-items: center;
        text-align: center;
        grid-gap: 2rem;
        width: 100%;
        margin-bottom: 2rem;
        &::before,
        &::after {
            content: "";
            border-top: 0.2rem solid var(--color-main-300);
        }
    }
}