.user-profile {
    &__main-title {
        @extend %site-title;
    }
    &__headshot {
        @extend %profile-headshot;
    }
    &__btn-upload {
        @extend %site-btn-upload;
    }
    &__btn-upload {
        @extend %site-btn-upload;
    }
    &__profile {
        @extend %profile-profile;
    }
    &__row {
        position: relative;
        @extend %form-row;
        label {
            @extend %site-label;
        }
        input {
            @extend %site-input;
        }
    }
    &__btn-update {
        @extend %btn-lg;
    }
    &__member-info {
        display: flex;
        gap: 1.2rem;
        align-items: center
    }
    &__upgrade {
        @extend %btn-base;
    }
    &__text {
        font-size: var(--font-size-base);
        font-weight: 900;
        color: var(--color-main-900);
        border: dotted 0.2rem transparent;
        border-radius: 1.2rem;
    }
    &__wrap {
        margin: 0 auto;
        width: 80rem;
        display: grid;
        grid-template-columns: 24rem 1fr;
        gap: 5.6rem;
        &.password {
            grid-template-columns: 1fr;
            width: 48rem;
            @include xs {
                width: 100%;
            }
        }
        &.certificate {
            grid-template-columns: 1fr;
        }
        @include xs {
            gap: 2.4rem;
            width: 100%;
            grid-template-columns: 1fr;
        }
        @include sm {
            width: 100%;
        }
        @include md {
            width: 100%;
        }
        @include lg {
            width: 100%;
        }
        @include xl {
            width: 100%;
        }
    }
    &__icon {
        width: 3.2rem;
        height: 3.2rem;
        display: block;
        background-color: transparent;
        &.level-1 {
            background-image: url('../images/icon-level-1.svg');
            background-size: cover;
        }
        &.level-2 {
            background-image: url('../images/icon-level-2.svg');
            background-size: cover;
        }
        &.level-3 {
            background-image: url('../images/icon-level-3.svg');
            background-size: cover;
        }
        &.level-4 {
            background-image: url('../images/icon-level-4.svg');
            background-size: cover;
        }
    }
    &__pet-type-wrap {
        display: flex;
        gap: 0.8rem;
        align-items: center;
        &.flex-wrap {
            flex-wrap: wrap;
        }
        &.flex-column {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__checkbox {
        @extend %site-checkbox;
        width: 100%;
    }
}