
.croppie-overlay {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 300;
    opacity: 0;
    pointer-events: none;
    background-color: rgba(0,0,0,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: opacity 300ms ease;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__inner {
        border-radius: 1.6rem;
        width: 48rem;
        height: 56rem;
        position: relative;
        padding: 2.4rem;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        gap: 3.2rem;
    }
    &__headshot {
        padding-bottom: 2.4rem;
    }
    &__upload-result {
        @extend %btn-base;
        width: 20rem;
        margin: 0 auto;
    }
}
