%btn-sm {
  box-sizing: content-box;
  background-color: var(--color-main-700);
  border-radius: 0.8rem;
  padding: 0 1.2rem;
  font-size: var(--font-size-sm);
  color: var(--color-main-100);
  transition: all 300ms ease;
  border: solid 0.1rem var(--color-main-700);
  font-weight: 700;
  height: 3.2rem;
  line-height: 3.2rem;
  text-align: center;
  &:hover {
    background-color: var(--color-main-900);
    border-color: var(--color-main-900);
  }
}

%btn-base {
  box-sizing: content-box;
  background-color: var(--color-main-700);
  border-radius: 0.8rem;
  padding: 0 1.6rem;
  font-size: var(--font-size-base);
  color: var(--color-main-100);
  transition: all 300ms ease;
  border: solid 0.1rem var(--color-main-700);
  font-weight: 700;
  height: 3.6rem;
  line-height: 3.6rem;
  text-align: center;
  &:hover {
    background-color: var(--color-main-900);
    border-color: var(--color-main-900);
  }
}

%btn-base-line {
  box-sizing: content-box;
  border: solid 0.1rem var(--color-main-700);
  background-color: transparent;
  border-radius: 0.8rem;
  padding: 0 1.6rem;
  font-size: var(--font-size-base);
  color: var(--color-main-8700);
  transition: all 300ms ease;
  font-weight: 700;
  color: var(--color-main-700);
  height: 3.6rem;
  line-height: 3.6rem;
  text-align: center;
  &:hover {
    border-color: var(--color-main-800);
    color: var(--color-main-800);
  }
}

%btn-lg {
  box-sizing: border-box;
  background-color: var(--color-main-800);
  border-radius: 1rem;
  padding: 0 2.4rem;
  font-size: var(--font-size-lg);
  color: var(--color-main-100);
  transition: background-color 300ms ease;
  border: 0;
  font-weight: 700;
  height: 5.2rem;
  line-height: 5.2rem;
  &:hover {
    background-color: var(--color-main-900);
  }
}

%site-title {
  font-size: clamp(var(--font-size-base), 10vw / 2, var(--font-size-2xl));
  color: var(--color-main-800);
  padding-bottom: 2rem;
  margin-bottom: 4rem;
  border-bottom: 0.1rem solid var(--color-main-300);
  position: relative;
  &::after {
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom: 0.1rem solid var(--color-main-600);
    transform: translate3d(0, 0.1rem, 0);
    width: 6rem;
    height: 0;
    content: "";
  }
  @include xs {
    margin-bottom: 2.4rem;
    padding-bottom: 1.2rem;
  }
}

%tag-list {
  display: flex;
  gap: 0.8rem;
  flex-wrap: wrap;
  margin-top: auto;
  margin-bottom: 0;
}

%btn-tag {
  white-space: nowrap;
  background-color: var(--color-main-200);
  color: var(--color-main-800);
  padding: 0.8rem 1.2rem;
  font-size: var(--font-size-sm);
  border-radius: 0.4rem;
  transition: background-color 300ms ease;
  border: solid 0.1rem var(--color-main-300);
  &:hover {
    background-color: var(--color-main-300);
  }
}

%post-info {
  display: flex;
  gap: 1.6rem;
  span {
    color: var(--color-main-700);
    font-size: var(--font-size-base);
    strong {
      color: var(--color-main-800);
      padding-left: 0.8rem;
    }
  }
}

%sm-label {
  color: var(--color-main-800);
  font-size: var(--font-size-base);
  display: flex;
  padding: 0.8rem 1.2rem;
  position: relative;
  border-radius: 0.8rem;
  border: solid 0.1rem var(--color-main-800);
  width: max-content;
}

%main-post-title {
  a {
    font-size: clamp(var(--font-size-base), -0.875rem + 8.333vw, var(--font-size-xl));
    line-height: 1.4;
    color: var(--color-main-800);
    &:hover {
      color: var(--color-main-900);
      transition: color 300ms ease;
    }
  }
}

%main-post-text {
  line-height: 1.6;
  font-size: clamp(var(--font-size-sm), -0.875rem + 8.333vw, var(--font-size-base));
  color: var(--color-main-800);
  letter-spacing: 0.2rem;
}

%post {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

%post-detail {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

%post-content {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

%post-img {
  img {
    object-fit: contain;
    max-width: 100%;
    display: block;
    margin: 0;
    &.lazy {
      opacity: 0;
    }
    &:not(.initial) {
      transition: opacity 1000ms, transform 300ms ease;
    }
    &.initial,
    &.loaded,
    &.error {
      opacity: 1;
    }
    &:not([src]) {
      visibility: hidden;
    }
  }
  a {
    background: var(--color-main-200);
    background: linear-gradient(to right, var(--color-main-200) 0%, var(--color-main-400) 50%, var(--color-main-200) 100%);
    background-size: 1000px 640px;
    border-radius: 1.2rem;
    aspect-ratio: 3 / 2;
    width: 100%;
    display: block;
    border-radius: 1.2rem;
    overflow: hidden;
    animation-duration: 3s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: ease-in-out;
    &:hover {
      img {
        transform: scale(1.05);
      }
    }
  }
}

%site-img {
  aspect-ratio: 3 / 2;
  background: var(--color-main-200);
  background: linear-gradient(to right, var(--color-main-200) 0%, var(--color-main-400) 50%, var(--color-main-200) 100%);
  background-size: 1000px 640px;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  border-radius: 1.2rem;
  overflow: hidden;
  img {
    object-fit: contain;
    max-width: 100%;
    display: block;
    margin: 0;
    border-radius: 1.2rem;
    &.lazy {
      opacity: 0;
    }
    &:not(.initial) {
      transition: opacity 1000ms, transform 500ms ease;
    }
    &.initial,
    &.loaded,
    &.error {
      opacity: 1;
    }
    &:not([src]) {
      visibility: hidden;
    }
  }
}

%post-row {
  display: flex;
  justify-content: center;
  padding-top: 4rem;
}

%site-radio {
  input[type=radio] {
    position: absolute;
    left: -9999px;
    box-sizing: border-box;
    padding: 0;
  }
  input[type=radio]:not(:checked)+label,
  input[type=radio]:checked+label {
    position: relative;
    padding-left: 2.8rem;
    cursor: pointer;
    height: 2.2rem;
    line-height: 2.2rem;
    display: inline-block;
    color: var(--color-main-800);
    font-size: var(--font-size-base);
  }
  input[type=radio]:not(:checked)+label:before,
  input[type=radio]:checked+label:before {
    content: '';
    position: absolute;
    left: 0;
    border-radius: 50%;
    width: 2.2rem;
    height: 2.2rem;
    background-color: var(--color-main-800);
    top: 0;
    box-sizing: border-box;
    @include xs {
      transform: translate3d(0, -0.1rem, 0);
    }
  }
  input[type=radio]+label:after {
    content: "";
    width: 1.2rem;
    height: 1.2rem;
    background-color: var(--color-main-200);
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(0.5rem, -0.6rem, 0);
    transition: all 300ms ease;
    opacity: 0;
    @include xs {
      transform: translate3d(0.5rem, -0.7rem, 0);
    }
  }
  input[type=radio]:checked+label:after {
    opacity: 1;
  }
}

%form-row {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

%site-label {
  font-size: var(--font-size-base);
  color: var(--color-main-700);
}

%site-input {
  background-color: var(--color-main-200);
  padding: 1.6rem;
  font-size: var(--font-size-base);
  border: 0;
  border-radius: 1.2rem;
  color: var(--color-main-900);
  transition: background-color 300ms ease;
  &::placeholder {
    color: var(--color-main-600);
  }
  &:focus {
    background-color: var(--color-main-300);
  }
  &.sm {
    padding: 1.2rem;
    font-size: var(--font-size-base);
    margin-left: 1.2rem;
  }
  &.full-width {
    width: 100%;
    margin-top: 0.8rem;
    margin-left: 0;
  }
  @include xs {
    &::-webkit-date-and-time-value{
      text-align:left;
    }
  }
}

%profile-wrap {
  width: 92rem;
  display: grid;
  grid-template-columns: min-content 1fr;
  gap: 8rem;
  &.single {
      width: 40rem;
      grid-template-columns: 1fr;
  }
}

%profile-headshot {
  width: 20rem;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  margin: 0 auto 2.4rem;
  @include xs {
    width: 16rem;
    margin-bottom: 0;
  }
  img {
    max-width: 100%;
    display: block;
    margin: 0;
    position: relative;
    z-index: 0;
    border-radius: 50%;
  }
}

%profile-profile {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

%site-btn-upload {
  width: 3.2rem;
  height: 3.2rem;
  position: absolute;
  right: 2rem;
  top: 16rem;
  background: transparent url('../images/btn-upload.svg') no-repeat 0 0;
  background-size: cover;
  border: 0;
  font-size: 0;
  @include xs {
    top: 13rem;
    right: 1rem;
  }
  input[type='file'] {
      cursor: pointer;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
  }
}

@keyframes placeHolderShimmer{
  0%{
      background-position: -468px 0
  }
  100%{
      background-position: 468px 0
  }
}

%body-title {
  font-size: var(--font-size-xl);
  color: var(--color-main-800);
  line-height: 1.6;
  letter-spacing: 0.2rem;
}

%body-text {
  font-size: var(--font-size-lg);
  line-height: 1.6;
  color: var(--color-main-800);
  letter-spacing: 0.2rem;
}

%sub-body-text {
  font-size: var(--font-size-base);
  line-height: 1.6;
  color: var(--color-main-800);
  letter-spacing: 0.2rem;
}

%site-select {
  box-sizing: border-box;
  width: 100%;
  height: 4.8rem;
  position: relative;
  overflow: hidden;
  &::before {
    width: 0.8rem;
    height: 0.8rem;
    pointer-events: none;
    z-index: 10;
    content: "";
    position: absolute;
    right: 2.4rem;
    top: 50%;
    transform: translate3d(0,-60%,0) rotate(45deg);
    border-right: solid 0.2rem var(--color-main-800);
    border-bottom: solid 0.2rem var(--color-main-800);
  }
  select {
    background-color: var(--color-main-200);
    border-radius: 1.2rem;
    border: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    height: 4.8rem;
    font-size: var(--font-size-base);
    padding: 0 1.6rem;
    color: var(--color-main-900);
    transition: all 300ms ease;
    &:focus {
      background-color: var(--color-main-300);
    }
    option {
        color: var(--color-grey-500);
    }
  }
}

%site-checkbox {
  width: 100%;
  input[type=checkbox] {
    position: absolute;
    left: -9999px;
    box-sizing: border-box;
    padding: 0;
  }
  input[type=checkbox]:not(:checked)+label,
  input[type=checkbox]:checked+label {
    position: relative;
    padding-left: 2.8rem;
    cursor: pointer;
    height: 2.2rem;
    line-height: 2.2rem;
    display: inline-block;
    color: var(--color-main-800);
    font-size: var(--font-size-base);
  }
  input[type=checkbox]:not(:checked)+label:before,
  input[type=checkbox]:checked+label:before {
    content: '';
    position: absolute;
    left: 0;
    border-radius: 0.6rem;
    width: 2.2rem;
    height: 2.2rem;
    background-color: var(--color-main-800);
    border-radius: 0.6rem;
    top: 0;
    box-sizing: border-box;
  }
  input[type=checkbox]+label:after {
    content: "";
    width: 1rem;
    height: 0.6rem;
    background: transparent url(../images/icon-check.svg) no-repeat center center;
    background-size: 1rem 0.6rem;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(0.6rem, -60%, 0);
    border: solid 0 transparent;
    transition: all 300ms ease;
    opacity: 0;
  }
  input[type=checkbox]:checked+label:after {
    opacity: 1;
  }
}

%first-block {
  padding-top: 8rem;
  @include xs {
      padding-top: 2.4rem;
      padding-left: 2.4rem;
      padding-right: 2.4rem;
  }
  @include sm {
      padding-top: 2.4rem;
  }
  @include md {
      padding-top: 3.2rem;
  }
  @include lg {
      padding-top: 3.2rem;
  }
}

@mixin rule-list {
  $self: &;
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 4rem;
        @include xs {
            gap: 2rem;
        }
        @include sm {
            gap: 2.4rem;
        }
        @include md {
            padding: 0 2.4rem;
            gap: 2.4rem;
        }
        @include lg {
            padding: 0 2.4rem;
            gap: 2.4rem;
        }
    }
    &__text {
        font-size: var(--font-size-base);
        color: var(--color-main-800);
        line-height: 1.6;
        @include xs {
          font-size: var(--font-size-sm);
        }
    }
    &__ol-list {
        padding-left: 5.6rem;
        list-style-type: cjk-ideographic;
        & > li {
            &::marker {
                font-size: var(--font-size-lg);
                color: var(--color-main-900);
                @include xs {
                  font-size: var(--font-size-base);
                }
            }
            & > h3 {
                font-size: var(--font-size-lg);
                color: var(--color-main-900);
                line-height: 1.6;
                margin-bottom: 1.2rem;
                @include xs {
                  font-size: var(--font-size-base);
                }
            }
            & > p {
                font-size: var(--font-size-base);
                color: var(--color-main-800);
                line-height: 1.6;
                @include xs {
                  font-size: var(--font-size-sm);
                }
            }
            &:not(:last-child) {
                margin-bottom: 5.6rem;
                @include xs {
                  margin-bottom: 4rem;
                }
            }
        }
    }
    &__ol-list-level-1 {
        list-style: decimal;
        padding-left: 2rem;
        & > li {
            font-size: var(--font-size-base);
            color: var(--color-main-800);
            line-height: 1.6;
            @include xs {
              font-size: var(--font-size-sm);
            }
            &:not(:last-child) {
                margin-bottom: 1.2rem;
            }
            & > h4 {
                font-size: var(--font-size-base);
                color: var(--color-main-800);
                line-height: 1.6;
                margin-bottom: 0.8rem;
                @include xs {
                  font-size: var(--font-size-sm);
                }
            }
        }
    }
    &__ol-list-level-2 {
        list-style-type: lower-alpha;
        padding-left: 2.4rem;
        & > li {
            font-size: var(--font-size-sm);
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1.2rem;
            }
            & > h5 {
                font-size: var(--font-size-sm);
                color: var(--color-main-800);
                line-height: 1.6;
                margin-bottom: 0.4rem;
            }
        }
    }
    &__ol-list-level-3 {
        list-style-type: lower-roman;
        padding-left: 1.6rem;
        & > li {
            font-size: var(--font-size-sm);
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1.2rem;
            }
        }
    }
}