.side-nav {
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    @include xs {
        display: block;
        white-space: nowrap;
        width: 100%;
        overflow-x: scroll;
        -webkit-overflow-scrolling: touch;
        padding-bottom: 1.6rem;
    }
    &__btn {
        font-size: var(--font-size-base);
        color: var(--color-main-600);
        line-height: 1.6;
        transition: all 300ms ease;
        font-weight: 700;
        &:hover {
            color: var(--color-main-800);
        }
        &.active {
            color: var(--color-main-800);
        }
        @include xs {
            display: inline-block;
            &:not(:last-child) {
                margin-right: 1.6rem;
            }
        }
    }
}