.user-pets {
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        width: 72rem;
        margin: 0 auto;
        @include xs {
            width: 100%;
        }
        @include sm {
            width: 100%;
        }
        @include md {
            width: 100%;
        }
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 4rem;
        @include xs {
            grid-template-columns: repeat(2, 1fr);
            gap: 2.4rem;
        }
    }
}

.pets-item {
    $self: &;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;
    &__img {
        img {
            max-width: 100%;
            display: block;
            margin: 0;
            border-radius: 50%;
        }
    }
    &__name {
        text-align: center;
        font-size: var(--font-size-xl);
        color: var(--color-main-700);
    }
    &__info {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.8rem;
    }
    &__gender {
        width: 2rem;
        height: 2rem;
        background-size: 2rem;
        &.male {
            background-image: url('../images/icon-male.svg');
        }
        &.female {
            background-image: url('../images/icon-female.svg');
        }
        &.die.male {
            background-image: url('../images/icon-male-die.svg');
        }
        &.die.female {
            background-image: url('../images/icon-female-die.svg');
        }
    }
    &__animal,
    &__age {
        font-size: var(--font-size-base);
        color: var(--color-main-700);
    }
    &__btn-add {
        width: 100%;
        aspect-ratio: 1/1;
        background-color: var(--color-main-900);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        font-size: var(--font-size-lg);
        color: var(--color-main-200);
    }
    &__btn-delete {
        @extend %btn-sm;
        background-color: var(--color-danger-500);
        border-color: var(--color-danger-500);
        width: max-content;
        &:hover {
            background-color: var(--color-danger-700);
            border-color: var(--color-danger-700);
        }
    }
}