$breakpoints: (
    "xs": 0,
    "sm": 480px,
    "md": 720px,
    "lg": 960px,
    "xl": 1200px,
    "xxl": 1600px,
    "xxxl": 1920px,
    "xxxxl": 2560px
);

@mixin xs {
    @media (min-width: map-get($breakpoints, "xs")) and (max-width: map-get($breakpoints, "sm")) {
        @content;
    }
}

@mixin sm {
    @media (min-width: map-get($breakpoints, "sm")) and (max-width: map-get($breakpoints, "md")) {
        @content;
    }
}

@mixin md {
    @media (min-width: map-get($breakpoints, "md")) and (max-width: map-get($breakpoints, "lg")) {
        @content;
    }
}

@mixin lg {
    @media (min-width: map-get($breakpoints, "lg")) and (max-width: map-get($breakpoints, "xl")) {
        @content;
    }
}

@mixin xl {
    @media (min-width: map-get($breakpoints, "xl")) and (max-width: map-get($breakpoints, "xxl")) {
        @content;
    }
}

@mixin xxl {
    @media (min-width: map-get($breakpoints, "xxl")) and (max-width: map-get($breakpoints, "xxxl")) {
        @content;
    }
}

@mixin xxxl {
    @media (min-width: map-get($breakpoints, "xxxl")) and (max-width: map-get($breakpoints, "xxxxl")) {
        @content;
    }
}

@mixin xxxxl {
    @media (min-width: map-get($breakpoints, "xxxxl")) {
        @content;
    }
}

@mixin breakpoint($bp: 0) {
    @media (min-width: $bp) {
        @content;
    }
}

%container {
    margin: 0 auto;
    box-sizing: border-box;
    @include xs {
        width: 100%;
    }
    @include sm {
        width: 100%;
        padding: 0 2.4rem;
    }
    @include md {
        width: 100%;
        padding: 0 2.4rem;
    }
    @include lg {
        width: 100%;
        padding: 0 2.4rem;
    }
    @include xl {
        width: 1080px;
    }
    @include xxl {
        width: 1440px;
    }
    @include xxxl {
        width: 1600px;
    }
    @include xxxxl {
        width: 1600px;
    }
}