.page-tab {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 4rem;
    @include xs {
        width: 100%;
        margin-bottom: 2rem;
        flex-wrap: wrap;
        gap: 0.8rem;
    }
    &__btn-tab {
        background-color: var(--color-main-300);
        padding: 1.2rem 2rem;
        color: var(--color-main-700);
        font-weight: 700;
        transition: background-color 300ms ease;
        font-size: var(--font-size-base);
        &:hover {
            background-color: var(--color-main-400);
        }
        &.active {
            background-color: var(--color-main-800);
            color: var(--color-main-100);
            pointer-events: none;
        }
        &:nth-child(1) {
            border-radius: 0.8rem 0 0 0.8rem;
            @include xs {
                border-radius: 0.8rem;
            }
        }
        &:last-child {
            border-radius: 0 0.8rem 0.8rem 0;
            @include xs {
                border-radius: 0.8rem;
            }
        }
        @include xs {
            border-radius: 0.8rem;
            flex: 0 0 calc(50% - 0.4rem);
            text-align: center;
        }
    }
}

.article-item {
    @extend %post;
    position: relative;
    &__post-img {
        @extend %post-img;
    }
    &__post-detail {
        @extend %post-detail;
    }
    &__post-content {
        @extend %post-content;
    }
    &__post-detail {
        @extend %post-detail;
    }
    &__main-post-title {
        @extend %main-post-title;
    }
    &__main-post-text {
        @extend %main-post-text;
    }
    &__tag-list {
        @extend %tag-list;
    }
    &__btn-tag {
        @extend %btn-tag;
    }
    &__info {
        @extend %post-info;
    }
    &__btn-delete {
        @extend %btn-base;
        background-color: var(--color-danger-500);
        position: absolute;
        right: 0.8rem;
        top: 0.8rem;
        z-index: 10;
        &:hover {
            background-color: var(--color-danger-700);
        }
    }
}

.site-pagination {
    padding-top: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    @include xs {
        flex-wrap: wrap;
    }
    &__page {
        display: flex;
        gap: 0.8rem;
        align-items: center;
    }
    &__btn-page {
        font-size: var(--font-size-sm);
        color: var(--color-main-700);
        border: solid 0.1rem var(--color-main-700);
        padding: 1rem 1.2rem;
        border-radius: 0.8rem;
        background-color: #fff;
        display: block;
        transition: all 300ms ease;
        font-weight: 700;
        &:hover {
            background-color: var(--color-main-700);
            color: #fff;
        }
        &.active {
            background-color: var(--color-main-700);
            color: #fff;
            pointer-events: none;
        }
        @include xs {
            padding: 0.8rem 1rem;
        }
    }
    &__status {
        font-size: var(--font-size-sm);
        color: var(--color-main-700);
    }
}

.error-tips {
    background-color: var(--color-danger-500);
    font-size: var(--font-size-sm);
    color: #fff;
    padding: 0.8rem;
    position: absolute;
    border-radius: 0.4rem;
    right: 0;
    top: 0;
    animation: up-down 400ms ease infinite;
    &::before {
        content: "";
        position: absolute;
        left: 2rem;
        bottom: -0.4rem;
        width: 0; 
        height: 0; 
        border-left: 0.4rem solid transparent;
        border-right: 0.4rem solid transparent;
        border-top: 0.4rem solid var(--color-danger-500);
    }
}

@keyframes up-down {
    0% {
        transform: translate3d(0,0,0);
    }
    50% {
        transform: translate3d(0,-5%,0);
    }
    100% {
        transform: translate3d(0,0,0);
    }
}