.site-header {
    background-color: var(--color-main-100);
    padding: 1.6rem 0;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    box-shadow: 0 0 0.4rem var(--color-main-500);
    @include xs {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__inner {
        @extend %container;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        width: 16.6rem;
        height: 4.2rem;
        background: transparent url('../images/logo-petpulse.png') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        a {
            width: 100%;
            height: 100%;
            font-size: 0;
            display: block;
        }
        @include xs {
            width: 16.6rem * 0.8;
            height: 4.2rem * 0.8;
            background-size: cover;
        }
    }
    &__btn-toggle {
        position: absolute;
        width: 3.2rem;
        height: 3.2rem;
        padding: 0;
        background-color: transparent;
        right: 2rem;
        top: 50%;
        transform: translate3d(0,-50%,0);
        z-index: 50;
        border-radius: 1.2rem;
        border: 0;
        display: none;
        @include xs {
            display: block;
        }
        @include sm {
            display: block;
        }
        @include md {
            display: block;
        }
        &::before,
        &::after {
            width: 2rem;
            height: 0.2rem;
            background-color: var(--color-main-700);
            position: absolute;
            left: 50%;
            top: 50%;
            content: "";
            border-radius: 1.2rem;
            transform-origin: 50% 50%;
            transition: transform 300ms ease-in-out;
        }
        &::before {
            transform: translate3d(-50%,-0.5rem,0);
        }
        &::after {
            transform: translate3d(-50%,0.3rem,0);
        }
        &.active {
            &::before {
                transform: translate3d(-50%,0,0) rotate(45deg);
            }
            &::after {
                transform: translate3d(-50%,0,0) rotate(-45deg);
            }
        }
    }
    &__overlay {
        display: block;
        position: absolute;
        left: 0;
        top: 7.2rem;
        width: 100%;
        height: calc(100vh - 7.2rem);
        opacity: 0;
        background-color: rgba(0,0,0,0.75);
        transition: opacity 300ms ease;
        pointer-events: none;
        &.active {
            opacity: 1;
            pointer-events: auto;
        }
        @include xs {
            top: 6.4rem;
            height: calc(100vh - 6.4rem);
        }
    }
    &__nav {
        display: flex;
        align-items: center;
        gap: 2rem;
        @include xs {
            width: 20rem;
            position: absolute;
            background-color: #fff;
            flex-direction: column;
            z-index: 25;
            top: 6.4rem;
            right: 0;
            height: calc(100vh - 6.4rem);
            padding: 2.4rem;
            box-sizing: border-box;
            transform: translate3d(100%,0,0);
            transition: transform 300ms ease-in-out;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        @include sm {
            width: 20rem;
            position: absolute;
            background-color: #fff;
            flex-direction: column;
            z-index: 25;
            top: 7.2rem;
            right: 0;
            height: calc(100vh - 7.2rem);
            padding: 2.4rem;
            box-sizing: border-box;
            transform: translate3d(100%,0,0);
            transition: transform 300ms ease-in-out;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        @include md {
            width: 20rem;
            position: absolute;
            background-color: #fff;
            flex-direction: column;
            z-index: 25;
            top: 7.2rem;
            right: 0;
            height: calc(100vh - 7.2rem);
            padding: 2.4rem;
            box-sizing: border-box;
            transform: translate3d(100%,0,0);
            transition: transform 300ms ease-in-out;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        @include lg {
            gap: 0.8rem;
        }
    }
    &__main-nav {
        display: flex;
        gap: 2rem;
        @include xs {
            flex-direction: column;
            width: 100%;
        }
        @include sm {
            flex-direction: column;
            width: 100%;
        }
        @include md {
            flex-direction: column;
            width: 100%;
        }
    }
    &__btn-link {
        font-size: var(--font-size-base);
        color: var(--color-main-800);
        transition: color 300ms ease;
        padding: 0.4rem 0;
        flex: 1;
        white-space: nowrap;
        &:hover {
            color: var(--color-main-900);
        }
        &.active {
            color: var(--color-main-900);
        }
    }
    &__user {
        display: flex;
        gap: 1.2rem;
        @include xs {
            flex-direction: column;
            width: 100%;
        }
        @include sm {
            flex-direction: column;
            width: 100%;
        }
        @include md {
            flex-direction: column;
            width: 100%;
        }
    }
    &__btn-login {
        @extend %btn-base;
        @include xs {
            flex: 1;
            text-align: center;
        }
        @include sm {
            flex: 1;
            text-align: center;
        }
    }
    &__btn-register {
        @extend %btn-base-line;
        @include xs {
            flex: 1;
            text-align: center;
        }
        @include sm {
            flex: 1;
            text-align: center;
        }
    }
    &__btn-sign-out {
        @extend %btn-base;
        @include xs {
            flex: 1;
            text-align: center;
        }
        @include sm {
            flex: 1;
            text-align: center;
        }
    }
    &__search {
        border-radius: 0.8rem;
        background-color: var(--color-main-200);
        padding: 0 3.6rem 0 1.6rem;
        display: flex;
        gap: 1.6rem;
        height: 4rem;
        align-items: center;
        position: relative;
    }
    &__search-icon {
        width: 2rem;
        height: 2rem;
        background: transparent url('../images/icon-search.svg') no-repeat 0 0;
        background-size: 2rem;
        position: absolute;
        top: 50%;
        transform: translate3d(0,-50%,0);
        right: 1.2rem;
    }
    &__search-input {
        width: 12rem;
        color: var(--color-main-900);
        border: 0;
        padding: 0;
        background-color: transparent;
        font-size: var(--font-size-base);
        &::placeholder {
            color: var(--color-main-600);
        }
        @include xs {
            width: 100%;
        }
        @include sm {
            width: 100%;
        }
        @include md {
            width: 100%;
        }
    }
    &__user-login {
        background-color: var(--color-main-200);
        padding: 0.8rem 1.6rem;
        border-radius: 8rem;
        display: flex;
        align-items: center;
        gap: 1.2rem;
        border: solid 0.1rem var(--color-main-300);
        @include xs {
            width: 100%;
        }
    }
    &__user-avatar {
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        border: solid 0.3rem transparent;
        position: relative;
        background-clip: padding-box;
        border: solid 0.3rem transparent;
        img {
            max-width: 100%;
            display: block;
            border-radius: 50%;
        }
    }
    &__user-level {
        width: 2.4rem;
        height: 2.4rem;
        &.level-1 {
            background: transparent url('../images/icon-level-1.svg');
            background-size: cover;
        }
        &.level-2 {
            background: transparent url('../images/icon-level-2.svg');
            background-size: cover;
        }
        &.level-3 {
            background: transparent url('../images/icon-level-3.svg');
            background-size: cover;
        }
    }
    &__user-name {
        font-size: var(--font-size-base);
        a {
            color: var(--color-main-800);
        }
    }
}