.latest-courses {
    $self: &;
    @include xs {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 4rem;
        @include xs {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include sm {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include md {
            grid-template-columns: 1fr;
            gap: 3.2rem;
        }
    }
    &__post-img {
        @extend %post-img;
    }
    &__post {
        @extend %post;
    }
    &__post-detail {
        @extend %post-detail;
    }
    &__post-content {
        @extend %post-content;
    }
    &__main-post-title {
        @extend %main-post-title;
    }
    &__info {
        @extend %post-info;
    }
    &__row {
        @extend %post-row;
    }
    &__btn-more {
        @extend %btn-lg;
    }
}