.about-pet-pulse {
    $self: &;
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        p {
            @extend %body-text;
        }
    }
}