.certificate {
    &__list {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__item {
        border: solid 0.1rem var(--color-main-200);
        background-color: var(--color-main-100);
        padding: 2.4rem;
        border-radius: 1.2rem;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(auto-fill, 1fr);
        gap: 0.4rem;
        @include xs {
            padding: 1.6rem;
            grid-template-columns: repeat(2, 1fr);
            gap: 0.8rem;
        }
    }
    &__title {
        font-size: var(--font-size-xl);
        line-height: 1.6;
        color: var(--color-main-800);
        grid-column: 1 / 4;
        grid-row: 1 / 2;
        @include xs {
            font-size: var(--font-size-lg);
            grid-column: 1 / 3;
        }
    }
    &__info {
        @extend %post-info;
        grid-column: 1 / 4;
        grid-row: 2 / 3;
    }
    &__btn-download {
        @extend %btn-base;
        width: max-content;
        grid-column: 4 / 5;
        grid-row: 1 / 2;
        margin-right: 0;
        margin-left: auto;
        @include xs {
            grid-column: 1 / 2;
            grid-row: 3 / 4;
            margin-right: auto;
            margin-left: 0;
        }
    }
}

.certificate-detail {
    background: var(--color-main-100) url('../images/certificate-detail-pattern.png') repeat center center;
    background-size: 7.5rem 5.8rem;
    border: double 1rem var(--color-main-700);
    aspect-ratio: 9 / 13;
    position: relative;
    box-sizing: border-box;
    padding: 5.6rem 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__pet-pulse {
        width: 12rem;
        margin: 0 auto;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__title,
    &__name,
    &__description {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
    }
    &__title {
        h4 {
            font-size: var(--font-size-2xl);
            color: var(--color-main-800);
            text-align: center;
        }
        h3 {
            text-align: center;
            font-size: var(--font-size-5xl);
            color: var(--color-main-800);
            font-weight: 900;
        }
    }
    &__name {
        h4 {
            font-size: var(--font-size-2xl);
            color: var(--color-main-800);
            text-align: center;
        }
        h3 {
            text-align: center;
            font-size: var(--font-size-6xl);
            color: var(--color-main-800);
            font-weight: 900;
            span {
                border-bottom: 0.4rem solid var(--color-main-800);
                padding-bottom: 0.8rem;
            }
        }
    }
    &__description {
        p {
            font-size: var(--font-size-2xl);
            line-height: 1.6;
            color: var(--color-main-800);
            text-align: center;
        }
    }
    &__common {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: flex-start;
    }
    &__date {
        padding-top: 4rem;
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        align-items: center;
        justify-content: center;
        p {
            text-align: center;
            &:nth-child(1) {
                color: var(--color-main-600);
                font-size: var(--font-size-base);
                line-height: 1.6;
            }
            &:nth-child(2) {
                color: var(--color-main-800);
                font-size: var(--font-size-lg);
                line-height: 1.6;
            }
        }
    }
    &__icon-certificate {
        width: 16rem;
        margin: 0 auto;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__teacher {
        padding-top: 4rem;
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        align-items: center;
        justify-content: center;
        p {
            text-align: center;
            color: var(--color-main-600);
            font-size: var(--font-size-base);
            line-height: 1.6;
        }
    }
    &__signature {
        width: 18rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__serial-number {
        p {
            color: var(--color-main-800);
            font-size: var(--font-size-2xl);
            line-height: 1.6;
            text-align: center;
        }
    }
}