.membership {
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__level-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        @include xs {
            grid-template-columns: 1fr;
            gap: 2.4rem;
        }
        @include sm {
            grid-template-columns: repeat(2, 1fr);
            gap: 2.4rem;
        }
        @include md {
            grid-template-columns: repeat(2, 1fr);
            gap: 2.4rem;
        }
        @include lg {
            gap: 2.4rem;
        }
        @include xl {
            gap: 2.4rem;
        }
    }
    &__level {
        display: flex;
        flex-direction: column;
        padding: 4rem 2.4rem;
        &:not(:last-child) {
            border-right: solid 0.2rem var(--color-main-200);
        }
    }
    &__title {
        font-size: var(--font-size-xl);
        text-align: center;
        color: var(--color-main-800);
        line-height: 1.6;
    }
    &__btn {
        @extend %btn-base;
        display: block;
    }
    &__level-image {
        width: 6.4rem;
        margin: 0 auto 2rem;
        img {
            max-width: 100%;
        }
    }
    &__row {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.8rem;
        &:not(:last-child) {
            border-bottom: solid 0.1rem var(--color-main-150);
            padding-bottom: 2.4rem;
            margin-bottom: 2.4rem;
        }
        small {
            @extend %sm-label;
        }
    }
    &__text {
        font-size: var(--font-size-sm);
        text-align: center;
        line-height: 1.6;
        color: var(--color-main-800);
    }
    &__remind-text {
        font-size: var(--font-size-sm);
        text-align: center;
        line-height: 1.6;
        color: var(--color-danger-700);
    }
    &__ul-lsit {
        list-style: disc;
        padding-left: 1.6rem;
        & > li {
            font-size: var(--font-size-base);
            line-height: 1.6;
            color: var(--color-main-800);
            font-weight: 900;
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
        }
    }
    &__ol-lsit {
        padding-top: 0.4rem;
        & > li {
            font-size: var(--font-size-base);
            line-height: 1.6;
            color: var(--color-main-800);
            position: relative;
            padding-left: 1.6rem;
            font-weight: normal;
            &::before {
                content: "";
                left: 0;
                position: absolute;
                width: 1.2rem;
                height: 1.2rem;
                background-color: var(--color-main-800);
                border-radius: 50%;
                top: 0;
                transform: translate3d(0,50%,0);
            }
            &::after {
                content: "";
                left: 0;
                position: absolute;
                width: 0.3rem;
                height: 0.4rem;
                border-right: solid 0.1rem #fff;
                border-bottom: solid 0.1rem #fff;
                top: 0;
                transform: translate3d(100%,170%,0) rotate(45deg);
            }
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
        }
    }
}