.pet-article-post {
    $self: &;
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__header {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        align-items: center;
        padding-bottom: 4rem;
    }
    &__title {
        font-size: clamp(var(--font-size-3xl), -0.875rem + 8.333vw, var(--font-size-5xl));
        color: var(--color-main-800);
        text-align: center;
        line-height: 1.2;
    }
    &__tag-list {
        @extend %tag-list;
    }
    &__btn-tag {
        @extend %btn-tag;
    }
    &__info {
        @extend %post-info;
    }
    &__post-img {
        @extend %post-img;
        border-radius: 1.2rem;
        overflow: hidden;
    }
    &__post-video {
        aspect-ratio: 16/9;
        position: relative;
        iframe {
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }
    &__purchasing {
        box-sizing: border-box;
        width: clamp(28rem, calc(100% - 2.4rem), 32rem);
        position: absolute;
        padding: 2.4rem;
        border-radius: 1.2rem;
        flex-direction: column;
        align-items: center;
        z-index: 10;
        left: 50%;
        top: 30%;
        transform: translate3d(-50%,0,0);
        background-color: #fff;
        gap: 2rem;
        display: none;
        pointer-events: none;
        box-shadow: 0 0 2rem var(--color-main-500);
        p {
            font-size: var(--font-size-base);
            color: var(--color-main-800);
            line-height: 1.6;
            text-align: center;
        }
        a {
            @extend %btn-base;
        }
    }
    &__wrap {
        width: 96rem;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 5.6rem;
        margin-bottom: 6.4rem;
        position: relative;
        @include xs {
            width: 100%;
        }
        @include sm {
            width: 100%;
        }
        @include md {
            width: 100%;
        }
        &.lock {
            height: 96rem;
            overflow: hidden;
            @include xs {
                height: 72rem;
            }
            @include sm {
                height: 72rem;
            }
            @include md {
                height: 72rem;
            }
            &::after {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(0deg, var(--color-main-150) 5%, rgba(255,255,255,0) 100%);
            }
            #{$self}__purchasing {
                display: flex;
                pointer-events: auto;
            }
        }
    }
    &__article-content {
        display: flex;
        flex-direction: column;
        gap: 6.4rem;
        position: relative;
    }
    &__row {
        display: flex;
        flex-direction: column;
        gap: 2.4rem;
        h2 {
            @extend %body-title;
        }
        p {
            @extend %body-text;
        }
    }
    &__more {
        width: 96rem;
        margin: 0 auto;
        font-size: var(--font-size-2xl);
        color: var(--color-main-600);
        font-weight: 700;
        line-height: 1.6;
        @include xs {
            width: 100%;
            font-size: var(--font-size-xl);
        }
        @include sm {
            width: 100%;
        }
        @include md {
            width: 100%;
        }
        a {
            color: var(--color-main-800);
            transition: color 300ms ease;
            padding-left: 1.6rem;
            margin-left: 1.6rem;
            border-left: solid 0.1rem var(--color-main-800);
            &:hover {
                color: var(--color-main-900);
            }
            @include xs {
                display: block;
                padding-left: 0;
                padding-top: 1.6rem;
                margin-left: 0;
                margin-top: 1.6rem;
                border-top: solid 0.1rem var(--color-main-300);
                border-left: 0;
            }
        }
    }
    &__control {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    &__btn-collect {
        @extend %btn-base;
    }
    &__share {
        display: flex;
        gap: 0.8rem;
        align-items: center;
        font-size: var(--font-size-base);
        color: var(--color-main-800);
    }
    &__btn-facebook {
        width: 3.2rem;
        height: 3.2rem;
        background: transparent url('../images/icon-facebook-color.svg') no-repeat 0 0;
        background-size: cover;
    }
    &__btn-line {
        width: 3.2rem;
        height: 3.2rem;
        background: transparent url('../images/icon-line-color.svg') no-repeat 0 0;
        background-size: cover;
    }
    &__nav {
        display: flex;
        gap: 3.2rem;
        justify-content: space-between;
        @include xs {
            flex-direction: column;
        }
    }
    &__btn-article {
        box-sizing: border-box;
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: 2.4rem 8.8rem;
        border: solid 0.2rem var(--color-main-200);
        border-radius: 1.6rem;
        position: relative;
        line-height: 1.4;
        background-color: var(--color-main-100);
        @include xs {
            padding: 2rem 5.6rem;
        }
        &::before {
            transform-origin: 50%;
            position: absolute;
            top: 50%;
            width: 2rem;
            height: 2rem;
            border-top: solid 0.2rem var(--color-main-600);
            border-right: solid 0.2rem var(--color-main-600);
            content: "";
        }
        span {
            &:nth-child(1) {
                font-size: var(--font-size-lg);
                color: var(--color-main-600);
                @include xs {
                    font-size: var(--font-size-base);
                }
            }
            &:nth-child(2) {
                font-weight: 700;
                font-size: var(--font-size-xl);
                color: var(--color-main-800);
                transition: color 300ms ease;
                @include xs {
                    font-size: var(--font-size-lg);
                }
            }
        }
        &:hover {
            span {
                &:nth-child(2) {
                    color: var(--color-main-900);
                }
            }
        }&:nth-child(1) {
            &::before {
                left: 4rem;
                transform: translate3d(0, -50%, 0) rotate(-135deg);
            }
            @include xs {
                padding-right: 2rem;
                &::before {
                    left: 2.4rem;
                }
            }
            @include sm {
                padding-right: 4rem;
            }
        }
        &:nth-child(2) {
            align-items: flex-end;
            &::before {
                right: 4rem;
                transform: translate3d(0, -50%, 0) rotate(45deg);
            }
            @include xs {
                align-items: flex-start;
                padding-left: 2rem;
                &::before {
                    right: 2.4rem;
                }
            }
            @include sm {
                padding-left: 4rem;
            }
        }
    }
}